import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { MeetingsComponent } from './meetings.component';
import { MeetingsReportsComponent } from './components/meetings-reports/meetings-reports.component';
import { MeetingsSetupComponent } from './components/meetings-setup/meetings-setup.component';
import { MeetingsViewComponent } from './components/meetings-view/meetings-view.component';
import { MeetingsCreateComponent } from './components/meetings-create/meetings-create.component';
import { MeetingsScheduledListComponent } from './components/meetings-scheduled-list/meetings-scheduled-list.component';
import { MeetingsListComponent } from './components/meetings-list/meetings-list.component';

const routes: Routes = [
	
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'create', component: MeetingsCreateComponent},
	{ path: 'settings', component: MeetingsSetupComponent},
	{ path: 'list', component: MeetingsListComponent},
	{ path: 'scheduled-list', component: MeetingsScheduledListComponent},
	{ path: 'view/:id', component: MeetingsViewComponent},
	{ path: 'reports', component: MeetingsReportsComponent},
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MeetingsRoutingModule { }
