import { Component, OnInit, Injector } from '@angular/core';
import { Router } from '@angular/router';
import * as _ from 'underscore';
import * as moment from 'moment';

import { MeetingService } from 'src/app/api/controllers/meeting';
import { ModalService } from 'src/app/shared/services/modal.service';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-meetings-list',
  templateUrl: './meetings-list.component.html',
  styleUrls: ['./meetings-list.component.scss']
})
export class MeetingsListComponent implements OnInit {

  isMeetingDataLoaded:boolean = false;
  totalMeetingItems:any;
  meetingFieldType:string = "meetingId";
  meetingOrder:boolean = false;
  selectedInput:string = "";
  columnField: any = {}
  lstMeetingData:any;
  isMobile:boolean = false;


  ItemMeetingStartIndex:any = 0;
  ItemMeetingEndIndex:any;
  itemMeetingLimit:any = 15;
  modalService:any;


  constructor(private router: Router,
    private meetingService:MeetingService,
    private injector: Injector,
    public dialog: MatDialog) { 
      this.modalService = this.injector.get(ModalService)
    }

  ngOnInit(): void {
    if(window.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;

    // this.meetingService.getMeetingByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res : any) => {
    
    let params = {
      apartmentId: 4
    }
    
    this.meetingService.getMeetingByApartmentId(params).subscribe((res : any) => {

      var meetingListData = res;
      console.log(res);
      //filter active true items
    //   this.lstMeetingData = meetingListData.filter(data => {
    //    return data.active
    //   })

    this.lstMeetingData = meetingListData;

      setTimeout(() => {

        

        console.log(this.lstMeetingData);
      }, 2000);

      this.totalMeetingItems = this.lstMeetingData.length;

      if(this.totalMeetingItems>this.itemMeetingLimit){
        this.ItemMeetingEndIndex = this.itemMeetingLimit;
      }
      else {
        this.ItemMeetingEndIndex = this.totalMeetingItems;
      }

      this.isMeetingDataLoaded = true;

    },
    error => {
      console.log(error);
    }
    );
  }
  addMeeting(){
    this.router.navigate(['ams/meetings/create']);
  }
  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  sortMeetingData(type){
    this.meetingFieldType = type;
    this.meetingOrder = !this.meetingOrder;
  }

  getFieldOrderBy(type) {
    if(this.meetingFieldType == type){
      return this.meetingOrder ? 'desc' : 'asc';
    }
    else return '';
  }
  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }
  selectColInput(value){
    console.log(value);
    this.selectedInput = value;
  }
  getMeetingIndexParams(event){
    this.ItemMeetingStartIndex = event.ItemMeetingStartIndex;
    this.ItemMeetingEndIndex = event.ItemMeetingEndIndex;
    this.itemMeetingLimit = event.ItemLimit;

    if(this.totalMeetingItems>this.itemMeetingLimit){
      this.ItemMeetingEndIndex = this.itemMeetingLimit;
    }
    else {
      this.ItemMeetingEndIndex = this.totalMeetingItems;
    }

  }
  addMeetingMinutes(data){
    this.modalService.showMeetingAddMinutesModal(data);
  }
  viewMeetingInfo(data){
    data.isEdit = false;
    this.modalService.showMeetingEditorViewModal(data);
  }
  editMeetingInfo(data){
    data.isEdit = true;
    this.modalService.showMeetingEditorViewModal(data);
  }

}
